<script setup>
import { ref, watch, nextTick, computed, watchEffect, onMounted } from 'vue';
import { ShadowRoot } from 'vue-shadow-dom';
import { useRoute } from 'vue-router';

// Store
import { useAppStore } from '@/store/app/app.js';
const appStore = useAppStore();

// Funcs
import { lmnFetch } from '@/assets/js/functions/lmnFetch.js';
import { isObject } from '@/assets/js/functions/isObject.js';
import { notify } from '@/assets/js/functions/notify.js';
import { hexToRgb } from '@/assets/js/functions/hexToRgb.js';
import { isNonProduction } from '@/assets/js/functions/isNonProduction.js';
import { useDarkMode } from './src/useDarkMode.js';
import { generateColorScheme } from './src/generateColorScheme.js';
import { settingsConfig } from './src/settings-config.js';
const { isDark } = useDarkMode();

// Comps
import Notifications from '@/components/utility/notifications/index.vue';
import AppIcon from '@/components/utility/icons/index.vue';
import AppButton from '@/components/utility/app-button/index.vue';
import LoadingIndicator from '@/components/utility/loading-indicator/index.ts';
import SettingsPanel from './src/settings-panel.vue';
import Drawer from '@/components/utility/drawer/index.vue';

// Refs
const route = useRoute();
const adoptedStyleSheets = ref(new CSSStyleSheet());
const loading = ref(false);
const saving = ref(false);
const selectedCategory = ref('general');
const uixpress_settings = ref({});
const originalSettings = ref({});
const settingsFetched = ref(false);
const settingsUploader = ref(null);
const drawer = ref(null);
const searchQuery = ref('');

const settingsCategories = [
  {
    value: 'general',
    label: __('General', 'uixpress'),
    icon: 'settings',
    description: __('Core plugin settings and license management', 'uixpress'),
  },
  {
    value: 'login',
    label: __('Login', 'uixpress'),
    icon: 'login',
    description: __('Customize login page appearance and security', 'uixpress'),
  },
  {
    value: 'theme',
    label: __('Theme', 'uixpress'),
    icon: 'palette',
    description: __('Colors, styling, and visual customization', 'uixpress'),
  },
  {
    value: 'whitelabel',
    label: __('White Label', 'uixpress'),
    icon: 'diamond_shine',
    description: __(
      'Rebrand and customize interface text and icons',
      'uixpress'
    ),
  },
  {
    value: 'menu',
    label: __('Menu', 'uixpress'),
    icon: 'menu',
    description: __('Admin menu behavior and interaction settings', 'uixpress'),
  },
  {
    value: 'posts',
    label: __('Posts', 'uixpress'),
    icon: 'article',
    description: __('Post management and display preferences', 'uixpress'),
  },
  {
    value: 'plugins',
    label: __('Plugins', 'uixpress'),
    icon: 'extension',
    description: __('Plugin management interface options', 'uixpress'),
  },
  {
    value: 'dashboard',
    label: __('Dashboard', 'uixpress'),
    icon: 'dashboard',
    description: __(
      'Custom dashboard and admin interface settings',
      'uixpress'
    ),
  },
  {
    value: 'analytics',
    label: __('Analytics', 'uixpress'),
    icon: 'analytics',
    description: __('Built-in analytics and tracking settings', 'uixpress'),
  },
  {
    value: 'media',
    label: __('Media', 'uixpress'),
    icon: 'image',
    description: __('Media library and upload management settings', 'uixpress'),
  },
  {
    value: 'users',
    label: __('Users', 'uixpress'),
    icon: 'people',
    description: __('User management and administration settings', 'uixpress'),
  },
  {
    value: 'comments',
    label: __('Comments', 'uixpress'),
    icon: 'comment',
    description: __(
      'Comment management and administration settings',
      'uixpress'
    ),
  },
  {
    value: 'security',
    label: __('Security', 'uixpress'),
    icon: 'security',
    description: __('Security settings and administration', 'uixpress'),
  },
  {
    value: 'database',
    label: __('Database', 'uixpress'),
    icon: 'database',
    description: __('Database explorer and management settings', 'uixpress'),
  },
];

// Function to get query parameter (with URL decoding)
const getQueryParam = (param) => {
  const urlParams = new URLSearchParams(window.location.search);
  const value = urlParams.get(param);
  return value ? decodeURIComponent(value) : value;
};

// Function to set query parameter (with URL encoding)
const setQueryParam = (param, value) => {
  const url = new URL(window.location);
  if (value) {
    url.searchParams.set(param, encodeURIComponent(value));
  } else {
    url.searchParams.delete(param);
  }
  window.history.replaceState({}, '', url);
};

/**
 * Filters settings based on search query
 * @returns {Array} Filtered settings array with match information
 */
const filteredSettings = computed(() => {
  if (!searchQuery.value || searchQuery.value.trim() === '') {
    return [];
  }

  const query = searchQuery.value.toLowerCase().trim();
  const matches = [];
  
  settingsConfig.forEach((setting) => {
    const labelMatch = setting.label?.toLowerCase().includes(query);
    const descriptionMatch = setting.description?.toLowerCase().includes(query);
    const idMatch = setting.id?.toLowerCase().includes(query);
    
    if (labelMatch || descriptionMatch || idMatch) {
      matches.push({
        ...setting,
        matchType: labelMatch ? 'label' : descriptionMatch ? 'description' : 'id',
      });
    }
  });
  
  return matches;
});

/**
 * Gets categories that contain matching settings
 * @returns {Set} Set of category values that have matching settings
 */
const categoriesWithMatches = computed(() => {
  if (!searchQuery.value || searchQuery.value.trim() === '') {
    return new Set();
  }
  
  return new Set(filteredSettings.value.map((setting) => setting.category));
});

/**
 * Filters categories based on search query
 * Includes categories that match directly OR contain matching settings
 * @returns {Array} Filtered categories array with match indicators
 */
const filteredCategories = computed(() => {
  if (!searchQuery.value || searchQuery.value.trim() === '') {
    return settingsCategories.map((cat) => ({ ...cat, hasMatchingSettings: false }));
  }

  const query = searchQuery.value.toLowerCase().trim();
  
  return settingsCategories
    .map((category) => {
      const labelMatch = category.label.toLowerCase().includes(query);
      const descriptionMatch = category.description.toLowerCase().includes(query);
      const valueMatch = category.value.toLowerCase().includes(query);
      const hasMatchingSettings = categoriesWithMatches.value.has(category.value);
      
      const categoryMatches = labelMatch || descriptionMatch || valueMatch;
      
      if (categoryMatches || hasMatchingSettings) {
        return {
          ...category,
          hasMatchingSettings,
          matchCount: hasMatchingSettings 
            ? filteredSettings.value.filter(s => s.category === category.value).length 
            : 0,
        };
      }
      
      return null;
    })
    .filter(Boolean);
});

// Function to update query parameter
const updateQueryParam = (category) => {
  const currentCategory = getQueryParam('category');

  // Only update if the query actually changed
  if (currentCategory !== category) {
    setQueryParam('category', category);
  }
};

// Watch selectedCategory and update query param
watch(
  selectedCategory,
  (newCategory) => {
    updateQueryParam(newCategory);
  },
  { immediate: false }
);

// Select category from query param
watch(
  () => route?.query,
  () => {
    const queryCategory = getQueryParam('category');
    if (
      queryCategory &&
      settingsCategories.find((c) => c.value === queryCategory)
    ) {
      selectedCategory.value = queryCategory;
    }
  },
  { immediate: true }
);

/**
 * Injects styles into shadow root
 */
const setStyles = () => {
  let appStyleNode = document.querySelector('#uixpress-settings-css');
  if (!appStyleNode) {
    appStyleNode = manuallyAddStyleSheet();
    appStyleNode.onload = () => {
      const appStyles = appStyleNode.sheet;
      for (const rule of [...appStyles.cssRules].reverse()) {
        adoptedStyleSheets.value.insertRule(rule.cssText);
      }
    };
  } else {
    const appStyles = appStyleNode.sheet;
    for (const rule of [...appStyles.cssRules].reverse()) {
      adoptedStyleSheets.value.insertRule(rule.cssText);
    }
  }
};

const manuallyAddStyleSheet = () => {
  var link = document.createElement('link');
  link.rel = 'stylesheet';
  link.href = `${appStore.state.pluginBase}app/dist/assets/styles/settings.css`;
  document.head.appendChild(link);
  return link;
};

/**
 * Fetches site settings from the server
 */
const getSettings = async () => {
  appStore.updateState('loading', true);
  loading.value = true;

  const args = { endpoint: 'wp/v2/settings', params: {} };
  const response = await lmnFetch(args);

  appStore.updateState('loading', false);
  loading.value = false;

  if (!response) return;

  if (
    !response.data.uixpress_settings ||
    !isObject(response.data.uixpress_settings)
  )
    return;

  uixpress_settings.value = response.data.uixpress_settings;

  if (uixpress_settings.value.custom_css) {
    uixpress_settings.value.custom_css = decodeHTMLEntities(
      uixpress_settings.value.custom_css
    );
  }

  originalSettings.value = JSON.parse(
    JSON.stringify(response.data.uixpress_settings)
  );

  nextTick(() => {
    settingsFetched.value = true;
  });
};

// Helper function to decode HTML entities
const decodeHTMLEntities = (text) => {
  const textArea = document.createElement('textarea');
  textArea.innerHTML = text;
  return textArea.value;
};

/**
 * Updates the site settings on the server
 */
const updateSettings = async () => {
  appStore.updateState('loading', true);
  saving.value = true;

  let payload = {
    uixpress_settings: JSON.parse(JSON.stringify(uixpress_settings.value)),
  };

  const args = {
    endpoint: 'wp/v2/settings',
    type: 'POST',
    params: {},
    data: payload,
  };
  const response = await lmnFetch(args);

  appStore.updateState('loading', false);
  saving.value = false;

  if (!response) return;

  notify({ title: __('Settings updated!', 'uixpress'), type: 'success' });
  appStore.updateState('uixpress_settings', uixpress_settings.value);
};

const exportSettings = () => {
  let payload = {
    uixpress_settings: JSON.parse(JSON.stringify(uixpress_settings.value)),
  };

  delete payload.uixpress_settings.license_key;
  delete payload.uixpress_settings.instance_id;

  const jsonString = JSON.stringify(payload, null, 2);
  const blob = new Blob([jsonString], { type: 'application/json' });
  const url = URL.createObjectURL(blob);

  const link = document.createElement('a');
  link.href = url;
  link.download = `uixpress-settings.json`;

  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);

  URL.revokeObjectURL(url);
};

const handleSettingsFileUpload = async (event) => {
  const file = event.target.files[0];
  try {
    await importFromSettingJSON(file);
    notify({ type: 'success', title: __('Settings imported', 'uixpress') });
  } catch (error) {
    notify({ type: 'error', title: __('Import failed', 'uixpress') });
  }
};

const importFromSettingJSON = async (file) => {
  try {
    if (!file || !(file instanceof File)) {
      notify({
        type: 'error',
        title: __('Please provide a valid JSON file', 'uixpress'),
      });
      return;
    }

    const fileContent = await file.text();
    const jsonData = JSON.parse(fileContent);

    if (!jsonData.uixpress_settings) {
      notify({
        type: 'error',
        title: __('Invalid settings: must be an object', 'uixpress'),
      });
      return;
    }

    if (
      typeof jsonData.uixpress_settings !== 'object' ||
      jsonData.uixpress_settings === null
    ) {
      notify({
        type: 'error',
        title: __('Invalid settings: must be an object', 'uixpress'),
      });
      return;
    }

    uixpress_settings.value = {
      ...uixpress_settings.value,
      ...jsonData.uixpress_settings,
    };
    return true;
  } catch (error) {
    notify({
      type: 'error',
      title: __('Error uploading settings', 'uixpress'),
    });
    throw error;
  }
};

const updateCssProperties = (cssvalues, colorName) => {
  for (let color of cssvalues) {
    const hexArray = hexToRgb(color.color);
    const variableName = `--uix-${colorName}-${color.step}`;
    document.documentElement.style.setProperty(
      variableName,
      hexArray.join(' ')
    );
  }
};

onMounted(() => {
  // Initialize selectedCategory from query param on mount
  const queryCategory = getQueryParam('category');
  if (
    queryCategory &&
    settingsCategories.find((c) => c.value === queryCategory)
  ) {
    selectedCategory.value = queryCategory;
  }
});

/* Base color watchers */
watch(
  () => uixpress_settings.value.base_theme_color,
  () => {
    if (!settingsFetched.value || !uixpress_settings.value.base_theme_color)
      return;
    uixpress_settings.value.base_theme_scale = generateColorScheme(
      uixpress_settings.value.base_theme_color
    );
    updateCssProperties(uixpress_settings.value.base_theme_scale, 'base');
  }
);

watch(
  () => uixpress_settings.value.base_theme_scale,
  () => {
    if (!settingsFetched.value) return;
    updateCssProperties(uixpress_settings.value.base_theme_scale, 'base');
  },
  { deep: true }
);

/* Accent color watchers */
watch(
  () => uixpress_settings.value.accent_theme_color,
  () => {
    if (!settingsFetched.value || !uixpress_settings.value.accent_theme_color)
      return;
    uixpress_settings.value.accent_theme_scale = generateColorScheme(
      uixpress_settings.value.accent_theme_color
    );
    updateCssProperties(uixpress_settings.value.accent_theme_scale, 'accent');
  }
);

watch(
  () => uixpress_settings.value.accent_theme_scale,
  () => {
    if (!settingsFetched.value) return;
    updateCssProperties(uixpress_settings.value.accent_theme_scale, 'accent');
  },
  { deep: true }
);

watch(
  () => uixpress_settings.value,
  () => {
    if (!settingsFetched.value) return;
    const settingsUpdateEvent = new CustomEvent('uipx-settings-update', {
      detail: uixpress_settings.value,
    });
    document.dispatchEvent(settingsUpdateEvent);
  },
  { deep: true }
);

setStyles();
getSettings();
</script>

<template>
  <component is="style">html{font-size:14px;}#wpcontent{padding:0}</component>
  <ShadowRoot
    tag="div"
    :adopted-style-sheets="[adoptedStyleSheets]"
    class="uipx-normalize"
  >
    <Notifications />
    <LoadingIndicator :height="3" />

    <div
      class="flex h-screen bg-zinc-50 dark:bg-zinc-950 text-zinc-900 dark:text-zinc-100 font-sans uipx-normalize max-h-[var(--uixp-body-height)] overflow-hidden gap-6 p-[1.5px]"
      :class="isDark ? 'dark' : ''"
    >
      <!-- Left Sidebar -->
      <div
        class="md:w-96 w-full flex flex-col bg-white dark:bg-zinc-800/30 rounded-2xl border border-zinc-200 dark:border-zinc-700/20 overflow-hidden max-h-full"
      >
        <!-- Header -->
        <div class="p-6">
          <div class="flex items-center gap-4 mb-4">
            <h1 class="text-xl font-medium grow">
              {{ __('Settings', 'uixpress') }}
            </h1>
            <div class="flex items-center">
              <AppButton
                type="transparent"
                @click="exportSettings"
                class="text-sm"
              >
                <AppIcon icon="download" class="text-xl" />
              </AppButton>
              <AppButton
                type="transparent"
                @click="settingsUploader.click()"
                class="text-sm"
              >
                <AppIcon icon="upload" class="text-xl" />
                <input
                  type="file"
                  accept=".json"
                  @change="handleSettingsFileUpload"
                  class="hidden"
                  ref="settingsUploader"
                />
              </AppButton>
            </div>
          </div>

          <!-- Search Input -->
          <div class="relative mb-4">
            <div class="absolute top-0 left-0 h-full flex items-center px-3 pointer-events-none">
              <AppIcon icon="search" class="text-zinc-400 text-sm" />
            </div>
            <input
              v-model="searchQuery"
              type="text"
              :placeholder="__('Search settings...', 'uixpress')"
              class="w-full pl-9 pr-3 py-2.5 text-sm border border-zinc-200 dark:border-zinc-700/40 rounded-xl bg-zinc-50 dark:bg-zinc-800/50 text-zinc-900 dark:text-zinc-100 placeholder-zinc-400 dark:placeholder-zinc-500 transition-all outline outline-transparent outline-offset-[-2px] focus:outline-indigo-300 dark:focus:outline-indigo-700 focus:shadow-xs focus:bg-white dark:focus:bg-zinc-800"
            />
            <button
              v-if="searchQuery"
              @click="searchQuery = ''"
              class="absolute top-0 right-0 h-full flex items-center px-3 text-zinc-400 hover:text-zinc-600 dark:hover:text-zinc-300 transition-colors"
            >
              <AppIcon icon="close" class="text-sm" />
            </button>
          </div>
        </div>

        <!-- Categories List -->
        <div class="flex-1 overflow-auto">
          <div
            v-if="loading && !settingsCategories.length"
            class="p-8 text-center"
          >
            <div
              class="w-12 h-12 bg-zinc-100 dark:bg-zinc-800 rounded-full flex items-center justify-center mx-auto mb-3"
            >
              <AppIcon
                icon="settings"
                class="text-zinc-400 text-xl animate-pulse"
              />
            </div>
            <p class="text-sm text-zinc-500">
              {{ __('Loading settings...', 'uixpress') }}
            </p>
          </div>

          <div v-else class="py-2 px-6 flex flex-col gap-1">
            <!-- No Results Message -->
            <div
              v-if="filteredCategories.length === 0"
              class="py-8 text-center"
            >
              <div
                class="w-12 h-12 bg-zinc-100 dark:bg-zinc-800 rounded-full flex items-center justify-center mx-auto mb-3"
              >
                <AppIcon icon="search" class="text-zinc-400 text-xl" />
              </div>
              <p class="text-sm text-zinc-500 dark:text-zinc-400">
                {{ __('No settings found', 'uixpress') }}
              </p>
              <p class="text-xs text-zinc-400 dark:text-zinc-500 mt-1">
                {{ __('Try a different search term', 'uixpress') }}
              </p>
            </div>

            <!-- Categories List -->
            <div
              v-for="category in filteredCategories"
              :key="category.value"
              @click="
                selectedCategory = category.value;
                drawer = true;
              "
              class="flex items-center gap-3 px-3 py-3 cursor-pointer transition-colors hover:bg-zinc-100 dark:hover:bg-zinc-800 rounded-xl transition-all cursor-pointer group"
              :class="
                selectedCategory === category.value
                  ? 'bg-zinc-100 dark:bg-zinc-800/60'
                  : ''
              "
            >
              <!-- Category Icon -->
              <div class="flex-shrink-0">
                <div
                  class="w-8 h-8 rounded-lg flex items-center justify-center"
                  :class="
                    selectedCategory === category.value
                      ? 'bg-indigo-100 dark:bg-indigo-900/50 text-indigo-600 dark:text-indigo-400'
                      : 'bg-zinc-100 dark:bg-zinc-800 text-zinc-500 dark:text-zinc-400'
                  "
                >
                  <AppIcon :icon="category.icon" class="text-sm" />
                </div>
              </div>

              <!-- Category Info -->
              <div class="flex-1 min-w-0">
                <div class="flex items-center gap-2 mb-0.5">
                  <span
                    class="font-medium text-sm truncate"
                    :class="
                      selectedCategory === category.value
                        ? 'text-zinc-900 dark:text-white'
                        : 'text-zinc-500 dark:text-zinc-400'
                    "
                  >
                    {{ category.label }}
                  </span>
                  <span
                    v-if="category.hasMatchingSettings && category.matchCount"
                    class="px-1.5 py-0.5 text-xs font-medium rounded-full bg-indigo-100 dark:bg-indigo-900/50 text-indigo-600 dark:text-indigo-400"
                  >
                    {{ category.matchCount }}
                  </span>
                </div>
                <div class="text-xs text-zinc-500 dark:text-zinc-400 truncate">
                  {{ category.description }}
                </div>
              </div>

              <!-- Arrow indicator -->
              <div
                class="flex-shrink-0 opacity-0 group-hover:opacity-100 transition-opacity"
              >
                <AppIcon icon="chevron_right" class="text-zinc-400 text-sm" />
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Right Content Area -->
      <div
        class="flex-1 flex flex-col bg-white dark:bg-zinc-800/30 rounded-2xl border border-zinc-200 dark:border-zinc-700/20"
      >
        <!-- Loading State -->
        <div v-if="loading" class="flex-1 flex items-center justify-center">
          <div class="text-center">
            <div
              class="w-16 h-16 bg-zinc-100 dark:bg-zinc-800 rounded-2xl flex items-center justify-center mx-auto mb-4"
            >
              <AppIcon
                icon="settings"
                class="text-2xl text-zinc-400 animate-pulse"
              />
            </div>
            <h3
              class="text-lg font-medium text-zinc-900 dark:text-zinc-100 mb-2"
            >
              {{ __('Loading Settings...', 'uixpress') }}
            </h3>
          </div>
        </div>

        <!-- Empty State -->
        <div
          v-else-if="!selectedCategory"
          class="flex-1 flex items-center justify-center"
        >
          <div class="text-center">
            <div
              class="w-16 h-16 bg-zinc-100 dark:bg-zinc-800 rounded-2xl flex items-center justify-center mx-auto mb-4"
            >
              <AppIcon icon="settings" class="text-2xl text-zinc-400" />
            </div>
            <h3
              class="text-lg font-medium text-zinc-900 dark:text-zinc-100 mb-2"
            >
              {{ __('Settings Panel', 'uixpress') }}
            </h3>
            <p class="text-sm text-zinc-500 mb-4">
              {{
                __(
                  'Select a category from the list to configure your settings.',
                  'uixpress'
                )
              }}
            </p>
          </div>
        </div>

        <!-- Settings Panel -->
        <div
          v-else
          class="flex-1 flex flex-col hidden md:flex max-h-full overflow-y-auto"
        >
          <SettingsPanel
            v-model="uixpress_settings"
            :tab="selectedCategory"
            :saving="saving"
            :categories="settingsCategories"
            :searchQuery="searchQuery"
            @save="updateSettings"
          />
        </div>

        <!-- Mobile Drawer -->
        <Drawer v-model="drawer" class="md:hidden" :showHeader="false">
          <SettingsPanel
            v-model="uixpress_settings"
            :tab="selectedCategory"
            :saving="saving"
            :categories="settingsCategories"
            :searchQuery="searchQuery"
            @save="updateSettings"
          />
        </Drawer>
      </div>
    </div>
  </ShadowRoot>
</template>

<style>
#wpbody,
#wpcontent {
  padding: 0 !important;
}

html {
  --uixp-body-height: calc(100vh - var(--uixp-toolbar-height) - 1rem);
}
</style>
